<template>
<div class="container" v-title="'易购商城'">
	<div class="main">
		<div class="banner">
			<mt-swipe>
			  <mt-swipe-item @change="handleChange" v-for="(item,index) in banner" :key="index">
			  	<img :src="item">
			  </mt-swipe-item>
			</mt-swipe>
		</div>
		<div class="top">
			<div class="notice">
				<img src="../assets/inform_icon.png">
				<p>[易购商城]毕业季促销啦！</p>
			</div>
			<div class="navbar">
					<div class="navbarItem" @click="linToKeyWord(1)">
						<img src="../assets/introduce_icon.png">
						<p>热销机型</p>
					</div>

					<div class="navbarItem" @click="linToKeyWord(2)">
						<img src="../assets/Shopping_icon.png">
						<p>品牌旗舰</p>
					</div>

					<div class="navbarItem" @click="linToKeyWord(3)">
						<img src="../assets/list_icon.png">
						<p>女神专享</p>
					</div>
				
					<div class="navbarItem" @click="linToKeyWord(4)">
						<img src="../assets/recharge_icon.png">
						<p>手机配件</p>
					</div>
			</div>
		</div>
		<div class="clear"></div>
		<div class="content">
			<div class="title">
				<h1>全部商品</h1>
				<h2>ALL GOODS</h2>
			</div>
			<div class="goodsContent" >
				<div class="goods"  v-for="item in goodsList">
						<img :src="item.goodsShowImg[0]" @click="linTogoodsDetail(item)">
						<p class="goodsName" @click="linTogoodsDetail(item)">{{ item.goodsName }}</p>
						<div class="buyPrice" @click="linTogoodsDetail(item)">
							<p class="price">¥{{ item.goodsPrice}}</p>
							<div class="buy">购买</div>
						</div>
				</div>
			</div>
		</div>
	</div>
	<tabbar ref="tabbar"></tabbar>
</div>

</template>

<script>
import axios from 'axios'
import { Base64 } from 'js-base64'
import tabbar from '../components/tabbar.vue'
import { MessageBox } from 'mint-ui'
export default {
  name: 'index',
  components: {
    tabbar
  },
  data () {
    return {
      memberLoginInfo:{
        openid:''
      },
      goodsList:[],
      dataList:{},
      banner: [
      	'https://img14.360buyimg.com/cms/jfs/t18601/124/2043088019/128895/de9fc7a8/5ae2881dNd1ab03fc.jpg',
      	'https://img10.360buyimg.com/da/jfs/t20050/102/423688136/85525/8d0bcbbb/5af41a60Nddc3442c.jpg!q90.jpg',
      	'https://img13.360buyimg.com/da/jfs/t17530/269/2465224697/98475/7fda2790/5af3a4e5N52fb4c2a.jpg!q90.jpg',
      	'https://img11.360buyimg.com/da/jfs/t17410/271/2441732548/96716/c9ab5911/5af4100dN1d66013a.jpg!q90.jpg'
      ]
    }
  },
  created () {
    let self = this
    if(localStorage.getItem("isLogin") == false || localStorage.getItem("isLogin") == null || localStorage.getItem("isLogin") == ''){
      this.$router.replace({path:'/login'})
    }

	//获取商品
    axios.get(this.serverUrl + '/goods/api/query').then(function (res) {
    	// debugger
        self.goodsList = res.data.data.pageList
        for (var i = 0; i < self.goodsList.length; i++) {
        	self.goodsList[i].goodsShowImg = eval(self.goodsList[i].goodsShowImg)
        	self.goodsList[i].goodsDetailImgs = eval(self.goodsList[i].goodsDetailImgs)
        }
    }).catch(function(err){
       alert(err)
    })

  },
  methods: {
	handleChange(index) {
	},
    linToKeyWord: function(num){
    	this.$router.push({name: 'hot', params: {keyWord: num}})
    },
    linTogoodsDetail:function(item){
    	this.$router.push({name: 'goodsDetail', params: {goodsDetail: JSON.stringify(item)}})
    }
  },
  mounted (){
  	let self = this
  	self.$refs.tabbar.hoverBgInfo('index')
  }
}
</script> 

<style scoped>
.container{}
.main{
	width: 100%;
	background-color: #f3f3f3;
	-webkit-overflow-scrolling : touch;
	position:absolute;
	top:0px;
	bottom:.85rem;
	overflow-y: scroll;
}
.banner{
	width: 100%;
	height: 3.4rem;
}
.bannerItem{
	width: 100%;
}
.banner img{
	width: 100%;
	height: 3.4rem;
	border-radius: .1rem;
}
.notice{
	width: 100%;
	height: .76rem;
	background-color: #fff;
	border-bottom: 1px solid #f0f0f0;
	float: left;
}
.notice img{
	width: .34rem;
	height: .34rem;
	float: left;
	margin: .21rem .14rem 0 .24rem;
}
.notice p{
	font-size: .24rem;
	color: #666;
	letter-spacing: .005rem;
}
.navbar{
	width: 100%;
	height: 2rem;
	float: left;
	background-color: #fff;
}
.navbarItem{
	float: left;
	width: 25%;
	height: 2rem;
	text-align: center;
}
.navbar img{
	width: 1.04rem;
	height: 1.04rem;
	margin-top: .3rem;
}
.navbarItem p{
	font-size: .24rem;
	color: #333;
	margin-top: -.2rem;
}
.content{
	width: 100%;
	height: auto;
}
.title{
	padding-top: .56rem
}
.title h1{
	font-size: .4rem;
	text-align:center;
	color: #666;
}
.title h2{
	text-align:center;
	font-size: .28rem;
	color: #666;
	margin-top: -.3rem;
	font-weight: normal;
	margin-bottom: .32rem;
}
.goodsContent{
	width: 96.7%;
	min-height: 50vw;
	padding-left: 3.3%;
}
.goods{
	width:45vw;
	float: left;
	background-color: #fff;
	padding-bottom: .2rem;
	margin-right: 3.3vw;
	margin-bottom: .24rem;
}
.deleteline{
  text-decoration: none;
}
.goods img{
	width: 100%;
	height: 45vw;
}
.goods .goodsName{
	height: .8rem;
	font-size: .28rem;
	color: #333;
	margin: -.2rem .12rem;
	overflow: hidden;
}
.buyPrice{
	width: 100%;
	margin-top: .36rem;
	margin-bottom: .14rem
}
.buyPrice .price{
	font-size: .32rem;
	color: #ff8452;
	margin: -.2rem .12rem;
}
.buyPrice .buy{
	height: auto;
	padding:.01rem .08rem;
	float: right;
	font-size: .24rem;
	color: #fff;
	background-color: #fe5454;
	border-radius: .08rem;
	margin-right: .16rem;
	margin-top: -.2rem
}
</style>


