<template>
<div class="container" v-title="'登录'">
  <h1>欢迎您登录</h1>
  <div class="content">
    <input class="nameInput" 
      type="tel"  
      pattern="[0-9]*" 
      name="" 
      placeholder="请输入手机号"
      v-model="memberLoginInfo.phone" />

    <input class="cardInput" 
      type="password" 
      name="" 
      placeholder="请输入密码" 
      v-model="memberLoginInfo.password" />
    <div class="submit" @click="submit()">提交</div>
    <router-view></router-view>
  </div>
</div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'login',
  data () {
    return {
      memberLoginInfo:{
        phone:'',
        password:'',
      }
    }
  },
  methods:{
    submit: function () {
      let self = this
      if(this.memberLoginInfo.phone == ''){
        alert('请填写手机号')
        return
      }
      localStorage.setItem("phone",this.memberLoginInfo.phone)
      localStorage.setItem("password",this.memberLoginInfo.password)
      axios({
        method: 'post',
        url: this.serverUrl + '/users/api/login',
        headers: {'Content-Type': 'application/json'},
        data: self.memberLoginInfo
        }).then(function (res) {
          localStorage.setItem("isLogin",true)
          sessionStorage.setItem('userInfo',JSON.stringify(res.data.data))
          self.$router.replace({path:'/my'})
      }).catch(function(err){
          alert(err)
      })
    }
  }
}
</script> 

<style scoped>
.container{
  width: 100%;
  height: 91%;
  padding-top: .24rem;
  background-color: #fff;
  overflow: auto;
  -webkit-overflow-scrolling : touch;
}
h1{
  font-size: .48rem;
  color: #333;
  font-weight: normal;
  text-align: center;
  margin-top: .6rem;
}
span{
  font-size: .26rem;
  color: rgba(239,84,85,0.8);
  position: absolute;
  right: .8rem;
  top: 2rem;
  border-left: .01rem solid #ddd;
  padding-left: .3rem;
}
.content{
  width: 100%;
  height: auto;
  background-color: #fff;
  border-radius: .1rem;
  padding-top: .3rem;
  padding-bottom: .68rem;
  position: relative;
}
.nameInput{
  width: 80%;
  margin-bottom: 0;
  padding-bottom: .1rem;
  margin-left: 10%;
  border: 0;
  border-bottom: .01rem solid rgba(239,84,85,0.5);
  position: absolute;
  top: 1rem;
  font-size: .24rem;
  color: #333;
  outline: none;
}
.cardInput{
  width: 80%;
  margin-bottom: 0;
  padding-bottom: .1rem;
  margin-left: 10%;
  border: 0;
  border-bottom: .01rem solid rgba(239,84,85,0.5);
  position: absolute;
  top: 2rem;
  font-size: .24rem;
  color: #333;
  outline: none;
}
::-webkit-input-placeholder {
　　color:#ccc;
}
.submit{
  width: 82%;
  height: .84rem;
  margin-top: 3.2rem;
  margin-left: 9%;
  background-color: #ef5455;
  border-radius: .42rem;
  font-size: .34rem;
  color: #fff;
  text-align: center;
  line-height: .84rem;
}
.register{
  font-size: .26rem;
  position: absolute;
  top:4.7rem;
  right: 42%;
  color: #ef5455;
}
</style>


