<template>
<div class="container" v-title="'新增地址'">
  <input class="nameInput name"  type="text" name="" placeholder="姓名" v-model="address.addressUserName" />
  <input class="nameInput tel"  type="number" name="" pattern="[0-9]*" placeholder="手机号码" v-model="address.addressUserPhone" />
  <input class="nameInput city"  type="text" name="" placeholder="省市区" v-model="address.addressProvince" />
  <input class="nameInput address"  type="text" name="" placeholder="详细地址" v-model="address.addressDetail" />
  <div class="fixedBottom" @click="submit()">+ 添加地址</div>
</div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'address',
  data () {
    return {
      address:{
        addressUserName: '',
        addressUserId: '',
        addressUserPhone: '',
        addressDetail: '',
        addressProvince: '',
      }
    }
  },
  created () {
    // debugger
    if(this.$route.params.goodsDetail != undefined){
      this.goodsDetail = JSON.parse(this.$route.params.goodsDetail)
    }
    if(this.$route.params.rootOrder != undefined){
      this.rootOrder = JSON.parse(this.$route.params.rootOrder)
    }
  },
  methods:{
    submit: function () {
      let self = this
      this.myInfo = JSON.parse(sessionStorage.getItem('userInfo'))
      this.address.addressUserId = this.myInfo.id
      if(this.address.addressUserName == '' || this.address.addressUserPhone == '' || this.address.addressProvince == '' || this.address.addressDetail == ''){
        alert('请输入完整信息')
        return
      }
      // if(this.address.addressUserPhone.length == 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/.test(this.address.addressUserPhone)){
      //   alert('请填写正确的手机号码！')
      //   return
      // }
      axios({
        method: 'post',
        url: this.serverUrl + '/address/api/add',
        headers: {'Content-Type': 'application/json'},
        data: self.address
      }).then(function (response) {
        alert('添加成功')
        if (self.$route.params.goodsDetail != undefined) {
            self.$router.replace({name: 'chooseAddress', params: {goodsDetail: JSON.stringify(self.goodsDetail)}})
          
        }
        else{
          window.history.go(-1)
        }
      })
        
      
    }
  }  
}
</script> 

<style scoped>
.container{
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #fff;
  -webkit-overflow-scrolling : touch;
}
.nameInput{
  outline: none;
  width: 92%;
  height: .4rem;
  margin-left: 4%;
  padding-bottom: .2rem;
  border: 0;
  border-bottom: .01rem solid #efefef;
  font-size: .26rem;
  color: #333;
  position: absolute;
}
::-webkit-input-placeholder {
　　color:#999;
}
.name{
  top: .2rem;
}
.tel{top: 1rem;}
.city{
 top: 1.8rem;
}
.address{
  top: 2.6rem;
}
.fixedBottom{
  width: 100%;
  height: .8rem;
  line-height: .8rem;
  position: fixed;
  bottom: 0;
  font-size: .26rem;
  color: #fff;
  text-align: center;
  background-color: #fe5454;
}
</style>


