<template>
<div class="container" v-title="'地址管理'">
  <!-- 将rootOrder 传入 -->
  <div v-if="address != '' && address != undefined">
    <div class="itemContent" @click="chooseAddress()">
      <h1>{{ address.addressUserName }}</h1>
      <h2>{{ address.addressUserPhone }}</h2>
      <h3>{{ address.addressProvince }}{{ address.addressDetail }}</h3>
      <span class="el-icon-arrow-right"></span>
    </div>
  </div>

  <div class="itemContent" @click="chooseAddress()" v-if="address == '' || address == undefined">
    <h3 style="position:absolute;top:-.2rem">请添加收货地址</h3>
    <span class="el-icon-arrow-right"></span>
  </div>

  <div class="orderContent">
    <div class="top">
      <div class="time" >{{goodsDetail.createDate}}</div>
    </div>

    <div class="middle goods">
    <!-- 从商品页传过来的参数 -->
      <div>
        <img :src="goodsDetail.goodsShowImg[0]">
      </div>
      <!-- 商品描述 -->
      <h1>{{ goodsDetail.goodsName }}</h1>
      
      <!-- 商品价格 -->
      <h2>¥{{ goodsDetail.goodsPrice }}</h2>

      <!-- 商品个数 -->
      <span>×{{ goodsDetail.goodsNum }}</span>

      <!-- 合计价格 -->
      <div class="total">
        <p>运费：¥{{ goodsDetail.goodsNum * goodsDetail.goodsCarriage }}</p>
      </div>
    </div>

    <div class="bottom">
      <p>合计：¥{{  goodsDetail.goodsNum * goodsDetail.goodsCarriage +  goodsDetail.goodsPrice}}</p>
    </div>
  </div>
  <div class="fixedBottom" @click="submitTakeGoods(goodsDetail)">确认</div>
  
</div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'address',
  data () {
    return {
      goodsDetail:{},
      address:{},
      takeGoodsFromRootOrderVo:{
        addressId:''
      },
      takeGoodsFromPointOrderVo:{
        addressId: '',
        pointOrderId: ''
      }
    }
  },
  created() {  
    let self = this
    //获取商品信息
    this.goodsDetail = JSON.parse(this.$route.params.goodsDetail)
    console.log(this.goodsDetail)
    // debugger
    
    //判断地址是从路由传过来的，还是直接获取的
    // debugger
    if(typeof(this.$route.params.address) != "undefined"){
      this.address = JSON.parse(this.$route.params.address)      
    }else{
      this.myInfo = JSON.parse(sessionStorage.getItem('userInfo'))    
      axios.get(this.serverUrl + '/address/api/query?keyword=' + this.myInfo.id).then(function (res) {
        self.address = res.data.data.pageList[0]
      })
    }
  },
  methods:{
    cancel:function() {
      this.popupTakeGoods = false
    },
    chooseAddress:function(){
      this.$router.replace({name: 'chooseAddress', params: {goodsDetail: JSON.stringify(this.goodsDetail)}})
    },
    submitTakeGoods: function(){
      let self = this
      if(this.address == undefined || this.address == ""){
        alert("请添加收货地址")
        return
      }
      // debugger
      if(this.goodsDetail.status != null){
        this.addressId = this.address.id
        this.cartId = this.goodsDetail.id
        this.num = this.goodsDetail.goodsNum
        axios.get(this.serverUrl + '/cart/api/buyGoods4Cart?cartId=' + this.cartId + '&num=' + this.num + '&addressId=' + this.addressId).then(function (res) {
            alert('购买成功！')
            sessionStorage.setItem('userInfo',JSON.stringify(res.data.data))
            self.$router.replace({name: 'myOrder'})
        }).catch(function(err){
           alert(err)
        })
      }else{
        this.userId = JSON.parse(sessionStorage.getItem('userInfo')).id
        this.goodsId = this.goodsDetail.id
        this.addressId = this.address.id
        axios.get(this.serverUrl + '/goods/api/buy?userId=' + this.userId + '&goodsId=' + this.goodsId + '&addressId=' + this.addressId).then(function (res) {
            alert('购买成功！')
            sessionStorage.setItem('userInfo',JSON.stringify(res.data.data))
            self.$router.replace({name: 'myOrder'})
        }).catch(function(err){
           alert(err)
        })
      }
      
    }
  }  
}
</script> 

<style scoped>
.container{
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #f3f3f3;
  -webkit-overflow-scrolling : touch;
}

.itemContent{
  width: 100%;
  height: 1.4rem;
  background-color: #fff;
}
.itemContent h1{
  font-size: .32rem;
  color: #333;
  font-weight: normal;
  position: absolute;
  left: .24rem;
  margin-right: .24rem;
  margin-top: .24rem;
  margin-bottom: 0
}
.itemContent h2{
  width: 40%;
  font-size: .32rem;
  color: #333;
  font-weight: normal;
  margin-top: .24rem;
  margin-bottom: 0;
  position: absolute;
  left: 1.6rem;
}
.itemContent h3{
  width: 70%;
  height: .4rem;
  font-size: .28rem;
  color: #999;
  font-weight: normal;
  margin-left: .24rem;
  margin-top: .74rem;
  margin-bottom: 0;
  position: absolute;
  left: 0;
}
.itemContent span{
  color: #ccc;
  font-size: .32rem;
  float: right;
  margin-right: .24rem;
  line-height: 1.4rem;
}
.fixedBottom{
  width: 100%;
  height: .8rem;
  line-height: .8rem;
  position: fixed;
  bottom: 0;
  font-size: .26rem;
  color: #fff;
  text-align: center;
  background-color: #fe5454;
}
.orderContent{
  width: 100%;
  height: auto;
  margin-top: .24rem;
  background-color: #fff;
}
.top{
  width: 100%;
  height: .8rem;
  line-height: .8rem;
}
.time{
  font-size: .26rem;
  color: #999;
  float: left;
  margin-left: .24rem;
}
.staus{
  font-size: .26rem;
  color: #ff8452;
  float: right;
  margin-right: .24rem;
}
.middle{
  width: 100%;
  height: 1.8rem;
  background-color: #f3f3f3;
}
.middle img{
  width: 1.5rem;
  height: 1.5rem;
  margin-left: .24rem;
  margin-right: .2rem;
  margin-top: .15rem;
  float: left;
  position: relative;
}
.middle h1{
  height: .8rem;
  font-size: .28rem;
  color: #333;
  font-weight: 500;
  line-height: .4rem;
  padding-top: .2rem;
  margin-top: 0;
  margin-bottom: 0
}
.middle h2{
  font-size: .28rem;
  color: #333;
  font-weight: 500;
  line-height: .8rem;
  margin-top: 0;
  margin-bottom: 0;
  float:left;
}
.middle span{
  font-size: .26rem;
  color: #999;
  float: right;
  margin-top: .3rem;
  margin-right: .24rem;
}
.total{
  width: 100%;
  height: .64rem;
  clear: both;
  
  color: #333;
  line-height: .64rem;
  background-color: #fff;
  border-bottom: .01rem solid #efefef;
}
p{
  float: right;
  margin-top: 0;
  margin-right: .24rem;
  margin-bottom: 0;
  font-size: .26rem;
}
.bottom{
  width: 100%;
  height:.74rem;
  line-height: .74rem;
  clear: both;
}
.confirm{
  color: #fe5455;
  border-right: .01rem solid #efefef;
}
.popContent{
  width: 5.4rem;
  background-color: #fff;
  border-radius: .26rem;
  text-align: center;
}
.popContent h1{
  margin-top: .42rem;
  margin-bottom: 0;
  font-size: .34rem;
  color: #333;
}
.popContent p{
  width: 90%;
  margin-left: 5%;
  margin-top: 0;
  margin-bottom: 0;
  padding-top: .2rem;
  padding-bottom: .3rem;
  font-size: .28rem;
  color: #666;
  border-bottom: .01rem solid #efefef;
}
.popContent .confirm,.cancel{
  float: left;
  width:45%;
  height: .9rem;
  margin-left: 3.5%;
  line-height: .9rem;
  font-size: .26rem;
}
</style>


