<template>
<div class="container" v-title="'我的'">
  <div class="main"> 
    <div class="user">
        <img :src="myInfo.userImg">
        <p>{{myInfo.userName}}</p>
        <p>¥{{myInfo.balance}} </p>
    </div>

    <div class="itemContent">

        <div class="myOrder" style="margin-top:.3rem">
          <div class="line1">
            <div class="title">我的订单</div>
            <div class="seeAll" @click="linkToOrder(3)">查看全部 ></div>
          </div>
          
          <div class="allOrder">
            <div class="Order" @click="linkToOrder(0)">
              <img src="../assets/delivery.png">
              <div class="orderName">待发货</div>
            </div>
            <div class="Order" @click="linkToOrder(1)">
              <img src="../assets/takeGoods.png">
              <div class="orderName">待收货</div>
            </div>
            <div class="Order" @click="linkToOrder(2)">
              <img src="../assets/evaluate.png">
              <div class="orderName">待评价</div>
            </div>
          </div>
        </div>

      <router-link to="/address">
        <div class="item" style="margin-top:.3rem">
          <div class="title">收货地址</div>
          <img src="../assets/next_icon.png">
        </div>
      </router-link>

      <router-link to="/balanceDetail">
        <div class="item">
          <div class="title">余额明细</div>
          <img src="../assets/next_icon.png">
        </div>
      </router-link>

      <router-link to="/withdraw">
        <div class="item" style="margin-top:.3rem">
          <div class="title">余额提现</div>
          <img src="../assets/next_icon.png">
        </div>
      </router-link>

      <router-link to="/reCharge">
        <div class="item">
          <div class="title">余额充值</div>
          <img src="../assets/next_icon.png">
        </div>
      </router-link>

    </div>

    <div class="exit" @click="logOff()">退出登录</div>
    
  </div>
  <tabbar ref="tabbar"></tabbar>
</div>
</template>

<script>
import tabbar from '../components/tabbar.vue'
import axios from 'axios'
export default {
  name: 'my',
  components: {tabbar},
  data () {
    return {
      myInfo:{     
        balance:'0',
        userImg:'',
        userName:''   
      },
      memberLoginInfo:{
        phone:'',
        password:'',
      }
    }
  },
  created () {
    if(sessionStorage.getItem('userInfo') == null){
      this.$router.replace({path:'/login'})
    }
    this.myInfo = JSON.parse(sessionStorage.getItem('userInfo'))
  },
  methods:{
    logOff:function(){
      localStorage.setItem("isLogin",false)
      this.$router.replace({path:'/login'})
    },
    linkToOrder:function(num){
      this.$router.push({name: 'myOrder', params: {key: num}})
    }
  },
  mounted (){
    let self = this
    self.$refs.tabbar.hoverBgInfo('my')
  }
}
</script> 

<style scoped>
.main{
  width: 100%;
  background-color: #f3f3f3;
  -webkit-overflow-scrolling : touch;
  position:absolute;
  top:0px; 
  bottom:.85rem;
  overflow-y: scroll;
}
.user{
  width: 100%;
  height: 3rem;
  background-color: #fe5455;
  text-align: center;
}
.user img{
  width: 1.3rem;
  height: 1.3rem;
  border-radius: 1.3rem;
  margin-top: .3rem;
}
.user p{
  text-align: center;
  font-size: .3rem;
  color: #fff;
  margin-top: -.1rem;
  height: .45rem;
  overflow: hidden;
}
.item{
  height: 1rem;
  line-height: 1rem;
  background-color: #fff;
  font-size: .28rem;
  color: #333;
  padding-left: .34rem;
  border-bottom: .01rem solid #efefef;
  border-bottom-width: 90%;
}
.item .title{
  float: left;
}
.item img{
  width: .24rem;
  height: .24rem;
  float: right;
  margin-top: .4rem;
  margin-right: .36rem;
}


.myOrder{
  height: 2rem;
  background-color: #fff;
  font-size: .28rem;
  color: #333;
  padding-left: .34rem;
  border-bottom-width: 90%;
}
.line1{
  width:100%;
  height:.8rem;
  /*border-bottom: .01rem solid #efefef;*/
  line-height:.8rem;
}
.myOrder .title{
  width: 50%;
  float: left;
}
.seeAll{
  float: right;
  margin-right: .3rem;
  color: #aaa;
  font-size: .22rem;
}
.allOrder{
  width: 100%;
  height: 1.1rem;
}
.Order{
  float: left;
  font-size: .24rem;
  color: #777;
  width: 31%;
  text-align: center;
  border-top: .01rem solid #efefef;
}
.allOrder img{
  width: .5rem;
  height: .5rem;
  margin-top: .15rem
}
.allOrder .orderName{
  font-size: .22rem;
}
.exit{
  height: .88rem;
  background-color: #fe5455;
  margin-top: .4rem;
  margin-bottom: .6rem;
  width: 84%;
  margin-left: 8%;
  border-radius: 30px;
  font-size: .34rem;
  color: #fff;
  line-height: .88rem;
  text-align: center;
}
</style>


