<template>
<div class="container" v-title="'充值'">
	<div class="content">
    <p class="name">账户金额：<span>{{myInfo.balance}}元</span></p>

    <p class="card">充值金额：</p>
    <input class="nameInput"  type="number"  name="" placeholder="请输入充值金额" v-model="balance" />

    <div class="moneyContent one" @click="userInput(num[0])">{{ num[0] }}元</div>
    <div class="moneyContent" @click="userInput(num[1])">{{ num[1] }}元</div>
    <div class="moneyContent" @click="userInput(num[2])">{{ num[2] }}元</div>
    <div class="moneyContent one two" @click="userInput(num[3])">{{ num[3] }}元</div>
    <div class="moneyContent two" @click="userInput(num[4])">{{ num[4] }}元</div>
    <div class="moneyContent two" @click="userInput(num[5])">{{ num[5] }}元</div>

    <p class="card rechargeWay">充值方式：</p>
    <div class="moneyContent one">银联卡</div>
    <!-- <div class="moneyContent" style="margin-left:.45rem">微信支付</div> -->

    <div class="submit" @click="submit()">下一步</div>
  </div>
</div>
</template>

<script>
import axios from 'axios'
import { Base64 } from 'js-base64'
import wx from 'weixin-js-sdk'
export default {
  name: 'reCharge',
  data () {
    return {
      num: [100, 200, 500, 1000, 1500, 2000] ,
      balance: '',
      myInfo:{}
    }
  },
  created () {
    this.myInfo = JSON.parse(sessionStorage.getItem('userInfo'))
  },
  methods:{
    userInput: function (num) {
      this.balance= num
    },
    submit: function () {
      if (this.balance == '' || this.balance == 0) {
        alert('输入金额有误')
        return
      }
      let self = this
      axios.get(this.serverUrl + '/goods/api/recharge?balance=' + this.balance + '&userId=' + this.myInfo.id).then(function (res) {
        sessionStorage.setItem('userInfo',JSON.stringify(res.data.data))
        alert('充值成功！')
        self.$router.replace({path:'/my'})
      }).catch(function(err){
         alert(err)
      })


      // axios({
      //   method: 'post',
      //   url:  '',
      //   headers: {'Content-Type': 'application/json'},
      //   data: self.reCharge
      // }).then(function (res) {
        
      // }).catch(function(err){
      //     alert(err)
      // })
    }
  }  
}
</script> 

<style scoped>
.container{
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #f3f3f3;
  padding-top: .24rem;
}
.content{
  width: 94vw;
  height: auto;
  margin-left: 3vw;
  background-color: #fff;
  border-radius: .1rem;
  box-shadow: .02rem .03rem #f3e0e0;
  padding-top: .45rem;
  padding-bottom: .68rem;
  position: relative;
}
.name{
  margin-top: 0;
  margin-bottom: 0;
  font-size: .32rem;
  color: #333;
  margin-left: .45rem;
}
.name span{
  color: #ff8452;
}
.nameInput{
  width: 85%;
  margin-bottom: 0;
  padding-bottom: .14rem;
  margin-left: .45rem;
  border: 0;
  border-bottom: .01rem solid #ef5455;
  position: absolute;
  top: 2.1rem;
  font-size: .28rem;
  color: #333;
}
input {outline:none;}
::-webkit-input-placeholder {
　　color:#999;
}
.card{
  margin-top: .5rem;
  margin-bottom: 0;
  font-size: .32rem;
  color: #333;
  margin-left: .45rem;
}
.rechargeWay{
  clear: both;
  position: absolute;
  top: 5rem;
}
.moneyContent{
  width: 1.55rem;
  height: .8rem;
  margin-left:.2rem;
  margin-top: 1.2rem;
  border: .01rem solid #efefef;
  border-radius: .1rem;
  font-size: .28rem;
  color: #333;
  text-align: center;
  line-height: .8rem;
  float: left;
}
.one{
  margin-left: .45rem;
}
.two{
  margin-top: .2rem;
}
.submit{
  width: 88%;
  height: .84rem;
  margin-top: 6rem;
  margin-left: 6%;
  background-color: #ef5455;
  border-radius: .42rem;
  font-size: .34rem;
  color: #fff;
  text-align: center;
  line-height: .84rem;
}
</style>



