<template>
<div class="container" v-title="'热销机型'">
  <div class="main">
  <div class="empty" v-if="isShow">
    暂无积分商品
  </div>
    <div class="goodsContent" v-if="goodsList.length > 0">
      <div class="goods" v-for="item in goodsList">
        <!-- <router-link class="deleteline" :to="{name: 'goodsDetail', params: {id:item.id}}"> -->
          <img :src="item.goodsShowImg[0]" @click="linTogoodsDetail(item)">
          <p class="goodsName" @click="linTogoodsDetail(item)">{{ item.goodsName }}</p>
          <div class="buyPrice" @click="linTogoodsDetail(item)">
            <p class="price">¥{{ item.goodsPrice}}</p>
            <div class="buy">购买</div>
          </div>
        <!-- </router-link> -->
      </div>
    </div>
    
  </div>
</div>
</template>

<script>
import axios from 'axios'
import { MessageBox } from 'mint-ui'
export default {
  name: 'index',
  data () {
    return {
      goodsList:[],
      isShow:false
    }
  },
  created () {
    //获取商品
    this.num = this.$route.params.keyWord
    let self = this
    axios.get(this.serverUrl + '/goods/api/query?keyword=' + this.num)
    .then(function (res) {
      self.goodsList = res.data.data.pageList
      for (var i = 0; i < self.goodsList.length; i++) {
        self.goodsList[i].goodsShowImg = eval(self.goodsList[i].goodsShowImg)
        self.goodsList[i].goodsDetailImgs = eval(self.goodsList[i].goodsDetailImgs)
      }
    })
    .catch(function(err){
      alert(err)
    })
  },
  methods:{
    linTogoodsDetail:function(item){
      this.$router.push({name: 'goodsDetail', params: {goodsDetail: JSON.stringify(item)}})
    }
  }
}
</script> 

<style scoped>
.container{}
.main{
  width: 100%;
  background-color: #f3f3f3;
  -webkit-overflow-scrolling : touch;
  position:absolute;
  top:0rem; 
  bottom: 0;
  overflow-y: scroll;
  padding-top: .2rem;
}
.confirmButton{
  font-size: .4rem;
  color: #fe5454;
}
.goodsContent{
  width: 96.7%;
  min-height: 50vw;
  padding-left: 3.3%;
}
.goods{
  width:45vw;
  float: left;
  background-color: #fff;
  padding-bottom: .2rem;
  margin-right: 3.3vw;
  margin-bottom: .24rem;
}
.goods .goodsName{
  height: .8rem;
  font-size: .28rem;
  color: #333;
  margin: -.2rem .12rem;
  overflow: hidden;
}
.deleteline{
  text-decoration: none;
}
.goods img{
  width: 100%;
  height: 45vw;
}
.goods p{
  font-size: .28rem;
  color: #333;
  margin: -.2rem .12rem;
}
.buyPrice{
  width: 100%;
  margin-top: .36rem;
  margin-bottom: .14rem
}
.buyPrice p{
  font-size: .32rem;
  color: #ff8452;
}
.buyPrice .buy{
  height: auto;
  padding:.01rem .08rem;
  float: right;
  font-size: .24rem;
  color: #fff;
  background-color: #fe5454;
  border-radius: .08rem;
  margin-right: .16rem;
  margin-top: -.2rem
}
.bg{
  font-size: .32rem;
  color: #333;
  text-align: center;
  margin-top: .3rem;
}
.empty{
  font-size: .3rem;
  color: #666;
  text-align: center;
  margin-top: .3rem;
}
</style>


