<template>
<div class="container" v-title="'余额明细'">
  <div class="itemContent noData" v-if="list.length <= 0">暂无数据</div>
  <div class="itemContent" v-for="item in list">
    <h1>{{item.goodsName }}</h1>
    <h2>{{item.createDate }}</h2>
    <h3 v-if="item.afterBalance > item.beforeBalance">+{{ item.actualMonetary}}</h3>
    <h3 v-if="item.beforeBalance > item.afterBalance" style="color:#333">-{{ item.actualMonetary}}</h3>
  </div>
</div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'pointDetail',
  data () {
    return {
      list:[]
    }
  },
  created (){
    let self = this
    this.myInfo = JSON.parse(sessionStorage.getItem('userInfo'))
    axios.get(this.serverUrl + '/cashFlow/api/query?keyword=' + this.myInfo.id)
    .then(function (res) {
      self.list = res.data.data.pageList
    })
  },
  methods:{
  }  
}
</script> 

<style scoped>
.container{
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #f3f3f3;
  -webkit-overflow-scrolling : touch;
}
.noData{
  font-size: .32rem;
  color: #666;
  text-align:center;
}
.itemContent{
  width: 100%;
  height: .7rem;
  background-color: #fff;
  position: relative;
  padding-top: .4rem;
  padding-bottom: .4rem;
  border-bottom: .01rem solid #efefef;
}
h1{
  font-size: .28rem;
  color: #333;
  margin-left: .24rem;
  margin-top: 0;
  margin-bottom: 0;
}
h2{
  font-size: .26rem;
  color: #999;
  font-weight: normal;
  margin-left: .24rem;
  margin-top: .1rem;
  margin-bottom: 0;
}
h3{
  font-size: .4rem;
  color: #ff8452;
  font-weight: normal;
  position: absolute;
  right: .24rem;
  top:.5rem;
  margin-left: .24rem;
  margin-top: 0;
  margin-bottom: 0;
}
</style>



