<template>
<div class="container" v-title="'购物车'">
  <div class="main">
    <div class="orderContent" v-for="item in order">
      <!-- 头部-->
      <div class="top">
        <div class="time">{{item.createDate}}</div>
        <div class="staus"  @click="delet(item)">删除</div>
      </div>

      <!--商品信息 -->
      <div class="middle goods" @click="linkToGoodsDetail()">
        <img :src="item.goods.goodsShowImg[0]">
        <h1>{{item.goods.goodsName}}</h1>
        <h2>¥{{item.orderPrice/item.goodsNums}}</h2>
        <span>x {{item.goodsNums}}</span>
        <div class="total">
        <p>共{{item.goodsNums}}件 订单金额：¥{{item.orderPrice}}</p>
        </div>
      </div>

      <!-- 底部 -->
      <div class="bottom">
        <div class="takeGoods" @click="takeGoods(item,0)" v-if="num == 0">提醒发货</div>
        <div class="takeGoods" @click="takeGoods(item,1)" v-if="num == 1">确认收货</div>
        <div class="takeGoods" @click="takeGoods(item,2)" v-if="num == 2">去评价</div>
      </div>
    </div>
  </div> 
  <!-- <tabbar ref="tabbar" ></tabbar> -->
</div>
</template>

<script>
import axios from 'axios'
import { MessageBox } from 'mint-ui'
export default {
  name: 'index',

  data () {
    return {
      num:0,
      order:{},
    }
  },
  created () {
    this.myInfo = JSON.parse(sessionStorage.getItem('userInfo'))
    this.num = this.$route.params.key
    let self = this
    axios.get(this.serverUrl + '/orders/api/query?keyword=' + this.myInfo.id + '&status=' + this.num)
    .then(function (res) {
        self.order = res.data.data.pageList    
        for(var i = 0;i < self.order.length;i++){
          self.order[i].goods.goodsShowImg = eval(self.order[i].goods.goodsShowImg)
        }     
    })
    .catch(function(err){
      alert(err.message)
    })
  },
  methods:{
    linkToGoodsDetail:function(item){
      this.$router.push({name: 'goodsDetail', params: {id:item.goods.goodsId}})
    },
    change:function(item,num){
      
    },
    takeGoods:function(item,num){
      let self = this
      this.orderId = item.id
      this.status = num + 1
      axios.get(this.serverUrl + '/orders/api/notifyOrder?orderId=' + this.orderId + '&status=' + this.status)
      .then(function (res) {
        if(num == 0){
          alert('已提醒发货')
        }else if(num == 1){
          alert('已确认收货')
        }else if(num == 2){
          alert('评价功能尚未开通')
        }
        self.$router.push({path:'/my'})
      })
      .catch(function(err){
        alert(err.message)
      })
    },
    delet: function(item){
      
    }
  }
}
</script> 

<style scoped>
.container{
  height: 100%;
  background-color: #f3f3f3;
  padding-top: .1rem;
}
.main{
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #f3f3f3;
  -webkit-overflow-scrolling : touch;
}
.deleteline{
  text-decoration: none;
}
.orderContent{
  width: 100%;
  height: auto;
  margin-top: .24rem;
  background-color: #fff;
}
.top{
  width: 100%;
  height: .8rem;
  line-height: .8rem;
}
.time{
  font-size: .26rem;
  color: #999;
  float: left;
  margin-left: .24rem;
}
.staus{
  font-size: .26rem;
  color: #ff8452;
  float: right;
  margin-right: .24rem;
}
.middle{
  width: 100%;
  height: 1.8rem;
  background-color: #f3f3f3;
}
.middle img{
  width: 1.5rem;
  height: 1.5rem;
  margin-left: .24rem;
  margin-right: .2rem;
  margin-top: .15rem;
  float: left;
  position: relative;
}
.middle h1{
  height: .8rem;
  font-size: .28rem;
  color: #333;
  font-weight: 500;
  line-height: .4rem;
  padding-top: .2rem;
  margin-top: 0;
  margin-bottom: 0
}
.middle h2{
  font-size: .28rem;
  color: #333;
  font-weight: 500;
  line-height: .8rem;
  margin-top: 0;
  margin-bottom: 0;
  float:left;
}
.middle span{
  font-size: .26rem;
  color: #999;
  float: right;
  margin-top: .3rem;
  margin-right: .24rem;
}
.total{
  width: 100%;
  height: .64rem;
  clear: both;
  font-size: .26rem;
  color: #333;
  line-height: .64rem;
  background-color: #fff;
  border-bottom: .01rem solid #efefef;
}
.total p{
  float: right;
  margin-top: 0;
  margin-right: .24rem;
  margin-bottom: 0
}
.bottom{
  width: 100%;
  height: 1.08rem;
  clear: both;
}
.bottom .takeGoods{
  width: 1.3rem;
  height: .6rem;
  float: right;
  margin-top: .24rem;
  margin-right: .24rem;
  border: .01rem solid #efefef;
  border-radius: .1rem;
  font-size: .26rem;
  color: #666;
  text-align: center;
  line-height: .6rem;
}
.bottom .sale{
  width: 1.3rem;
  height: .6rem;
  float: right;
  margin-top: .24rem;
  margin-right: .2rem;
  border: .01rem solid #fe5455;
  border-radius: .1rem;
  font-size: .26rem;
  color: #fe5455;
  text-align: center;
  line-height: .6rem;
}
.popContent{
  width: 5.4rem;
  background-color: #fff;
  border-radius: .26rem;
  text-align: center;
}
.popContent h1{
  margin-top: .42rem;
  margin-bottom: 0;
  font-size: .34rem;
  color: #333;
}
.popContent p{
  width: 90%;
  margin-left: 5%;
  margin-top: 0;
  margin-bottom: 0;
  padding-top: .2rem;
  padding-bottom: .3rem;
  font-size: .28rem;
  color: #666;
  border-bottom: .01rem solid #efefef;
}
.popContent .confirm,.cancel{
  float: left;
  width:45%;
  height: .9rem;
  margin-left: 3.5%;
  line-height: .9rem;
  font-size: .26rem;
}
.confirm{
  color: #fe5455;
  border-right: .01rem solid #efefef;
}
.guessJO,.openRedPacket{
  width: 88%;
  height: .78rem;
  margin-left: 6%;
  border-radius: .5rem;
  line-height: .78rem;
  margin-top: .32rem;
  font-size: .32rem;
  color: #fff;
}
.guessJO{
  background-color: #fe5455;
}
.openRedPacket{
  margin-bottom: .48rem;
  background-color: #ff8452;
}
.empty{
  font-size: .32rem;
  color: #666;
  text-align: center;
  margin-top: .3rem;
}
.Logistics{
  width: 100%;
  text-align: center;
  margin-left: 0
}
.minusPlus{
  width: .55rem;
  height: .55rem;
  margin-top: .28rem;
  background-color: #ebebeb;
  font-size: .35rem;
  color: #999;
  text-align: center;
  line-height: .55rem;
  float: left;
  border-radius: .05rem;
}
.edit{
  font-size: .26rem;
  float: right;
  margin-top: .26rem;
  margin-right: .2rem;
  color: #999
}
</style>


