<template>
<div class="container" v-title="'商品详情'">
<div class="main">
		<!-- 商品展示图 -->
		<div class="top">
			<div class="banner">
				<mt-swipe>
				  <mt-swipe-item @change="handleChange" v-for="item in goodsDetail.goodsShowImg">
				  	<img :src="item">
				  </mt-swipe-item>
				</mt-swipe>
			</div>
			<h1>{{ goodsDetail.goodsName }}</h1>
			<h2><span>¥ </span>{{ goodsDetail.goodsPrice}}</h2>
			<h3>运费:{{ goodsDetail.goodsCarriage}}元</h3>
		</div>
		<div class="middle quantity">
			<div class="num">数量:</div>
			<div class="minusPlus" @click="minus()"><span class="el-icon-minus"></span></div>
			<div class="goodsNum" v-model='goodsNum'>{{goodsNum}}</div>
			<div class="minusPlus" @click="plus()"><span class="el-icon-plus"></span></div>
		</div>
		<div class="bottom goodsDetail">
			<span class="el-icon-goods"></span>
			<h1> &nbsp;商品详情</h1>
			<div v-for="item in goodsDetail.goodsDetailImgs"  style="margin-bottom:-.346rem">
				<img :src="item">
			</div>
		</div>
	</div>
	<div class="fixedBottom">
		<p>余额：<span>¥{{ myInfo.balance}}</span></p>
		<div class="buy" @click="buyConfirm(goodsDetail)">购买</div>
		<div class="buy1" @click="addShopCart(goodsDetail)">加入购物车</div>
	</div>
</div>
</template>

<script>
import axios from 'axios'
import { MessageBox } from 'mint-ui'
export default {
  name: 'index',
  data () {
    return {
    	buy:true,
    	rootOrder:{},
    	goodsDetail:{},
    	popupBuy:false,
    	goodsDetail:{},
    	goodsNum: 1,
    	shopCart:{
    		goodsId:'',
    		goodsNum:'',
    		userId:''
    	}
    }
  },
  created () {
    let self = this
    if(sessionStorage.getItem('userInfo') != null){
    	this.myInfo = JSON.parse(sessionStorage.getItem('userInfo'))
    }
    this.goodsDetail = JSON.parse(this.$route.params.goodsDetail)
    // debugger
    console.log(this.goodsDetail)
  },
  methods:{
  	handleChange(index) {
	},
  	minus: function () {
  		if (this.goodsNum > 1 ) {
  			this.goodsNum--
  		}
  	},
  	plus: function(){
  		this.goodsNum++
  	},
  	submitSale:function(){  //确认参与促销
      this.popupBuy = false
      this.popupUpdate = true      
    },
  	buyConfirm:function(goodsDetail){
  		// debugger
  		goodsDetail.goodsNum = this.goodsNum 
  	  if(goodsDetail.goodsPrice * this.goodsNum >  this.myInfo.balance){
  	  	alert('余额不足，请充值后购买！')
  	  	return
  	  }
  	  this.$router.push({name: 'takeGoods', params: {goodsDetail: JSON.stringify(goodsDetail)}})
    },
    addShopCart:function(goodsDetail){
  		// debugger
  		let self = this
  		this.shopCart.goodsId = this.goodsDetail.id
  		this.shopCart.goodsNum = this.goodsNum
  		this.shopCart.userId = this.myInfo.id
  		axios({
	        method: 'post',
	        url:this.serverUrl + '/cart/api/add',
	        headers: {'Content-Type': 'application/json'},
	        data: this.shopCart
	        }).then(function (res) {
	        	alert("已添加")
	        	self.$router.push({name: 'shopCart'})
	      }).catch(function(err){
	          alert(err)
	      })  	  
    }
  }  
}
</script> 

<style scoped>
.container{}
.main{
  width: 100%;
  background-color: #f3f3f3;
  -webkit-overflow-scrolling : touch;
  position:absolute;
  top:0px; 
  bottom:.98rem;
  overflow-y: scroll;
}
.deleteline{
  text-decoration: none;
}
.top{
	border-bottom: 0.02rem solid #efefef;
	background-color: #fff;
}
.banner{
	width: 100%;
	height: 104vw;
}
.top img{
	width: 100%;
	height: 100vw;
}
.top h1{
	font-size: .28rem;
	font-weight: normal;
	margin-left: .24rem;
	margin-right: .24rem;
	margin-top: -.2rem
}
.top span{
	font-size: .32rem;
	color: #ff8452;
}
.top h2{
	font-size: .44rem;
	color: #ff8452;
	margin-left: .24rem;
	margin-top: -.1rem;
}
.top h3{
	font-size: .24rem;
	color: #999;
	font-weight: normal;
	margin-top: -.4rem;
	margin-left: .24rem;
	margin-right: .24rem;
	margin-bottom: .2rem;
}
.middle{
	height: 1.04rem;
	background-color: #fff;
	/*background-color: #ccc;*/
}
.middle .num{
	font-size: .28rem;
	color: #333;
	line-height: 1.04rem;
	margin-left: .24rem;
	margin-right: .24rem;
	float: left;
}
.goodsNum{
	font-size: .28rem;
	color: #333;
	line-height: 1.04rem;
	margin-left: .24rem;
	margin-right: .24rem;
	margin-top: .05rem;
	float: left;
}
.minusPlus{
	width: .55rem;
	height: .55rem;
	margin-top: .28rem;
	background-color: #ebebeb;
	font-size: .35rem;
	color: #999;
	text-align: center;
	line-height: .55rem;
	float: left;
	border-radius: .05rem;
}
.numButton{
	width: .7rem;
	height: .5rem;
	float: left;
	background-color: #fe5455;
	border-radius: .05rem;
	margin-right: .24rem;
	margin-top: .3rem;
	font-size: .28rem;
	color: #fff;
	text-align: center;
	line-height: .5rem;
}
.bottom{
	width: 100%;
	height: auto;
	margin-top: .2rem;
	background-color: #fff;
	padding-bottom: .3rem;
	padding-top: .1rem;
}
.bottom img{
	width: 100%;
}
.bottom  span{
	font-size: .32rem;
	color: #333;
	margin-left: .24rem;
	float: left;
	line-height: .78rem
}
.bottom h1{
	font-size: .32em;
	color: #333;
	margin-left: .24rem;
	font-weight: normal;
	line-height: .36rem
}
.fixedBottom{
	width: 100%;
	height: .98rem;
	border-top: .01rem solid #efefef;
	position: fixed;
	bottom: 0;
	background-color: #fff;
}
.fixedBottom p{
	width: 55%;
	font-size: .28rem;
	color: #666;
	margin-left: .24rem;
	float: left;
	line-height: .5rem;
}
.fixedBottom span{
	font-size: .32rem;
	color: #ff8452;
	font-weight: bold;
}
.fixedBottom .buy1{
	width: 21%;
	height: .98rem;
	float:right;
	background-color: #f0f0f0;
	font-size: .3rem;
	color: #777;
	text-align: center;
	line-height: .98rem;
}
.fixedBottom .buy{
	width: 20%;
	height: .98rem;
	float:right;
	background-color: #fe5455;
	font-size: .3rem;
	color: #fff;
	text-align: center;
	line-height: .98rem;
}

.popContent{
  width: 5.4rem;
  background-color: #fff;
  border-radius: .26rem;
  text-align: center;
}
.popContent h1{
  margin-top: .42rem;
  margin-bottom: 0;
  font-size: .34rem;
  color: #333;
}
.popContent p{
  width: 90%;
  margin-left: 5%;
  margin-top: 0;
  margin-bottom: 0;
  padding-top: .2rem;
  padding-bottom: .3rem;
  font-size: .28rem;
  color: #666;
  border-bottom: .01rem solid #efefef;
}
.popContent .confirm,.cancel{
  float: left;
  width:45%;
  height: .9rem;
  margin-left: 3.5%;
  line-height: .9rem;
  font-size: .26rem;
}
.confirm{
  color: #fe5455;
  border-right: .01rem solid #efefef;
}

.guessJO,.openRedPacket{
  width: 88%;
  height: .78rem;
  margin-left: 6%;
  border-radius: .5rem;
  line-height: .78rem;
  margin-top: .32rem;
  font-size: .32rem;
  color: #fff;
}
.guessJO{
  background-color: #fe5455;
}
.openRedPacket{
  margin-bottom: .48rem;
  background-color: #ff8452;
}
</style>


